body {
  font-family: Arial, Helvetica, sans-serif;
  background: #333;
  color: #ffffff;
  line-height: 1.6;
  height: 100vh; /* Matters for scroll behaviour */
  overflow: hidden; /* Hides scroll bar */
}

.home_content {
  width: 100%;
  height: 100vh; /* For some reason, use 100vh here instead of 100% */

  /* CSS Smooth Scroll */
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}


.navbar {
  position: fixed;
  top: 0;
  z-index: 1;
  display: flex;
  width: 100%;
  height: 60px;
  background: rgba(0, 0, 0, 0.7);
}

.navbar ul {
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: center;
}

.navbar ul li {
  margin: 0 1rem;
  padding: 1rem;
}

.navbar ul li a {
  text-decoration: none;
  text-transform: uppercase;
  color: #f4f4f4;
}

.navbar ul li a:hover {
  color: skyblue;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100vh;
  scroll-snap-align: center;
  overflow-x:hidden;
  background-color: #333;
}

section h1 {
  font-size: 4rem;
  color: #f4f4f4;
}

section p {
  font-size: 1.5rem;
  color: #f4f4f4;
}

section p small {
  font-size: 1rem;
  color: #f4f4f4;
}

section ul {
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  max-width: 100vw;
}

section ul::-webkit-scrollbar {
  display: none;
}

section ul li {
  min-width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
  text-align: center;
  position: relative;
}

.section_content {
  position: absolute;
  top: 40%;
  text-align: center;
  color: white;
  width: 100%;
  left: 0;
  font-size: calc(1rem + 3vw);
}

